.Main-container {
    height: 100vh;
    width: 100vw;
    font-family: "Courier New", sans-serif;
}

.Navbar-container.mobile {
    position: relative;
    width: 100vw;
    height: 7.5vh;
    float: top;
}
  
.Navbar-container.desktop {
    position: relative;
    width: 100vw;
    height: 7.5vh;
    float: top;
}

#settingsicon {
    padding: 0.75vh;
    display: block;
    position: absolute;
    max-height: 80%;
    max-width: 80%;
    float: left;
}

.modal {
    font-size: 12px;
    font-family: "Courier New", sans-serif;
    text-align: center;
}

.modal > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 2em;
    padding: 5px;
}

.modal > .bottombuttons {
    padding: 10px;
}

#closebutton {
    font-family : inherit;
    font-size: 2em;
}

.Video-container.mobile {
    background-color: #e6e6e6;
    position: relative;
    width: 100vw;
    height: calc(100vw * (640 / 480));
    float: top;
}
  
.Video-container.desktop {
    background-color: #e6e6e6;
    position: relative;
    width: calc(92.5vh * (640 / 480));
    height: 92.5vh;
    float: left;
}

.InfoBox-container.mobile {
    position: relative;
    width: 100vw;
    height: calc(100vh - (100vw * (640 / 480)));
    float: top;
    text-align: center;
}
  
.InfoBox-container.desktop {
    position: relative;
    width: calc(100vw - (100vh * (640 / 480)));
    height: 92.5vh;
    float: left;
    text-align: center;
}

.checkbutton {
    font-size: 2em;
    padding: 10px;
    font-family : inherit;
}

#LeftSide {
    width: 50%;
    height: 98%;
    float: left;
    padding-top: 2%;
}

#RightSide {
    width: 50%;
    height: 100%;
    float: right;
}